const swiper = new Swiper('.about-team_slider', {
  // Optional parameters
  slidesPerView: 'auto',
  spaceBetween: 20,
  // Navigation arrows
  navigation: {
    nextEl: '.swiper-arrow.next',
    prevEl: '.swiper-arrow.prev',
  },
});

const swiperModal = new Swiper('.about-team_modal-slider .max-width-full', {
  // Optional parameters
  slidesPerView: 1,
  direction: 'vertical',
  spaceBetween: 20,
  mousewheel: {
    enabled: true,
    forceToAxis: true,
    thresholdDelta: 25,
  },
  freeMode: false,
  shortSwipes: false,
});
